<template>
  <div class="my-lawler">
    <v-container>
      <v-row class="text-center bg-primary-gradient">
        <v-col cols="3">
          <v-img
              :src="require('../../assets/icons/home-01.png')"
              contain
              height="90"
          />
        </v-col>

        <v-col cols="8" align-self="center">
          <h1 style="font-size: 1.5rem" class="font-weight-bold profile-title">Mi abogado LegalHelp</h1>
        </v-col>
      </v-row>

      <v-row class="breadcrumbs">
        <v-col cols="12">Tipo de consulta > Tipo de pago > Tarjeta</v-col>
      </v-row>

      <v-row>
        <v-col>
          <div class="page-title text-capitalize">{{ service.name }}</div>

          <div class="call-to-action">
            <div class="text text-center" style="display: block">Precio final: S/. {{ service.price_final }}</div>
          </div>


          <div class="page-title">Ingresar los datos de tu tarjeta</div>

          <div class="call-to-action" @click="goToBank()">
            <div class="text" style="display: block">
              <v-text-field
                  counter="25"
                  label="Numero de tarjeta *"
              ></v-text-field>

              <v-text-field
                  label="Nombre del titular *"
              ></v-text-field>

              <v-text-field
                  label="Vencimiento *"
                  hint="MM/AA"
                  persistent-hint
              ></v-text-field>

              <v-text-field
                  label="Codigo de seguridad *"
                  hint="Últimos 3 números en el reverso"
                  persistent-hint
              ></v-text-field>
            </div>
          </div>

          <div class="text-center" style="margin: 2rem 0">
            <v-btn color="primary" width="160" @click="goToConfirm()" :loading="isLoading">
              Continuar
              <v-img :src="require('../../assets/icons/lawyer-04.svg')" height="20" contain/>
            </v-btn>
          </div>

        </v-col>
      </v-row>

      <payment></payment>
    </v-container>
  </div>
</template>

<script>
import {mapActions, mapGetters} from "vuex";
import axios from 'axios'
import Payment from "../../components/Payment";

export default {
  name: 'MyLawyerPayment',
  components: {Payment},
  inject: ['notyf'],
  data: () => ({
    isLoading: false
  }),
  computed: {
    ...mapGetters(['user', 'services', 'service']),
  },
  created() {
    if (this.service === null) {
      let serviceId = parseInt(this.$route.params.id)
      let service = this.services.find(obj => obj.id === serviceId)
      this.selectService(service)
    }
  },
  mounted() {

  },
  methods: {
    ...mapActions(['selectService']),
    goToConfirm() {
      this.isLoading = true

      let serviceId = this.$route.params.id
      let url = 'consultations';
      let data = {
        service_id: this.service.id,
        payment_method_id: 1,
        status_id: 3,
        coupon_code: this.service.coupon_code,
      }

      axios.post(url, data).then((response) => {
        this.$router.push({name: 'MyLawyerPaymentConfirm', params: {id: serviceId, operationId: response.data.id}})
      }).catch(() => {
        this.notyf.error('Ups! Ocurrio un error, por favor intentelo mas tarde.');
      }).then(() => {
        this.isLoading = false
      })
    },
  }
}
</script>
